// used ul =class css-li-border-bottom  li class active > a
.css-li-border-bottom {
  li {
    position: relative;
    display: inline-block;
    &:not(:last-child) {
      margin-right: 10px;
    }
    a {
      color: var(--color-first-header-text);
      font-size: 17px;
      font-weight: 500;
      display: block;
      padding: 2px 0;
      position: relative;
      &::after {
        position: absolute;
        content: "";
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background-color: var(--color-third-header-border);
        transition: all 0.5s;
        transform: scale(0);
      }
      &.active {
        &::after {
          transform: scale(1);
        }
      }
    }
    &:hover a:after {
      transform: scale(1);
    }
  }
}
